/* ============================================ *
 * Header
 * ============================================ */



.page-header {
    max-width: $max-container-width;
    margin: 0 auto;
    padding: 0 $trim;
    min-height: 40px;
}

.page-header-container {
    position: relative;
}


.header-language-container,
.page-header {
    font-family: $f-stack-sans;
}

/* ============================================ *
 * Language switcher + welcome message
 * ============================================ */

.header-language-background {
    padding: 0 $gap;
    background-color: $c-action;
    text-transform: uppercase;
    height: 40px;
    margin-bottom: 30px;

    @include bp(max-width, $bp-xlarge) {
        margin-bottom: 15px;
    }

    .header-language-container {
        max-width: $max-content-width;
        margin-left: auto;
        margin-right: auto;
    }

    &:after {
        @include clearfix;
    }

    .store-language-container {
        float: left;
        font-size: 32px;
        line-height: 40px;
        color: $c-text-light-purple;

        @include bp(max-width, $bp-xlarge) {
            font-size: $f-size-xxxl;
        }

        span { font-weight: 900 }
    }
    .right-links {
        float: right;
        color: $c-text-white;
        font-size: $f-size-xxl;
        line-height: 1;
        margin-top: 10px;
        font-weight: 300;

        @include bp(max-width, $bp-xlarge) {
            font-size: $f-size;
            margin-top: 13px;
        }

        @include bp(max-width, $bp-medium) {
            width: 410px;
        }

        p { 
            display: inline-block;
            border-right: 1px solid #FFF;
            padding-right: 15px;
            span { text-transform: lowercase; }
        }

        .label { display: inline; }

        .skip-link.skip-account {
            line-height: 1;
            height: initial;
            background: none;
            font-weight: 600;

            @include bp(max-width, $bp-medium) {
                float: right;
            }
        }
    }
}

.page-header-container .store-language-container {
    float: right;
    max-width: 35%;
    padding: 13px 15px 10px 10px;

    label {
        display: none;
    }
    select {
        max-width: 100%;
    }
}

@include bp(max-width, $bp-xsmall) {
    .page-header-container .store-language-container {
        padding-top: 8px;

        select {
            // Setting this font-size will causing zooming on iOs devices, but it keeps header more tidy
            font-size: $f-size-xs;
        }
    }
}

.header-language-background,
.header-language-background a {
    color: $c-text-white;
}

@include bp(max-width, $bp-small) {

    .header-language-background {
        display: none;
    }

}

/* ============================================ *
 * Logo
 * ============================================ */

.logo {
    display: inline-block;
    float: left;
    width: 200px;
    min-width: 100px;
    min-height: 44px;
    text-align: center;
    margin-top: -10px;

    @include bp(min-width, $bp-xlarge + 1) {
        max-width: 200px;
        min-height: 90px;
    }

    @include bp(max-width, $bp-xlarge) {
        max-width: 160px;
    }

    img {
        max-width: 100%;
    }

    .small {
        display: block;
        @include bp(min-width, $bp-medium + 1) { display: none; }
    }
    .large {
        display: none;
        @include bp(min-width, $bp-medium + 1) { display: block; }
    }
}

.logo:hover {
    opacity: 0.8;
}


/* ============================================ *
 * Skip Links
 * ============================================ */

.skip-links {
    clear: both;
    overflow: hidden;
    border-top: 1px solid #EDEDED;
    border-bottom: 1px solid #EDEDED;

    @include bp(min-width, $bp-large + 1) { border: 0; }
}


/* -------------------------------------------- *
 * Skip - Link
 */

.skip-link {
    position: relative;
    float: left;
    width: 20%;
    height: 44px;
    background: #FFFFFF;
    color: $c-text;
    line-height: 1;
    text-align: center;
}

a.skip-link {
    text-decoration: none;
}

.skip-link:not(.skip-active):hover {
    opacity: 0.8;
}

// ---------------------------------------------

@include bp(max-width, $bp-medium) {

    .skip-link {
        border-right: 1px solid #EDEDED;
    }

    .skip-link:last-child {
        border-right: 0;
    }

}

// ---------------------------------------------

@include bp(min-width, 500px) {

    .skip-link {
        width: 25%;
    }

}

/* -------------------------------------------- *
 * Skip Link - Active
 */
@include bp(max-width, $bp-medium) {

    .skip-link.skip-active {
        background: #EDEDED;
        color: inherit;
    }

    .skip-link.skip-active span {
        color: inherit;
    }

}

/* -------------------------------------------- *
 * Skip Link - Icon
 */

.skip-link .icon {
    display: inline-block;
    width: 30px;
    height: 30px;
    vertical-align: middle;
}

.skip-link.skip-nav .icon,
.skip-link.skip-cart:not(.no-count) .icon {
    margin-right: 3px;
}

/* -------------------------------------------- *
 * Skip Link - Label
 */

.skip-link .label {
    display: none;
}


/* ============================================ *
 * Skip Content
 * ============================================ */

.skip-content {
    display: none;
}

.skip-content.skip-active {
    display: block;
    background: #EDEDED;
}

/* ============================================ *
 * Global Header Navigation
 * ============================================ */

@include bp(max-width, $bp-medium) {
    // ---------------------------------------------
    // Link

    #header-account li a,
    .nav-primary a.level0 {
        padding: 0 15px 0 25px;
        border-bottom: 1px solid $c-module-border;
        text-align: left;
        color: $c-text;
        text-transform: uppercase;
        line-height: 30px;
    }

    #header-account li:last-child a,
    .nav-primary li.level0:last-child a.level0 {
        border-bottom: 0;
    }

    .no-touch #header-account a:hover,
    .no-touch .nav-primary a:hover {
        background-color: $c-module-background;
        text-decoration: none;
    }

}

.account-cart-wrapper {
    position: absolute;
    top:0px;
    right:0px;

    @include bp(max-width, $bp-medium) {
        position: static;
    }
}

#header {
    @include bp(max-width, $bp-small) {
        margin-top: 20px;
    }

    .nav-right { 
        float: right; 
        margin-top: 10px;
        margin-right: -22px;

        @include bp(max-width, $bp-xlarge) {
            margin-top: -5px;
        }

    }
}
