/* ============================================ *
 * CMS Pages
 * ============================================ */
.cms-page-view .breadcrumbs { opacity: 0 }
// ----------------------------------------------
// Content for static pages

.cms-page-view,
.cms-no-route {
    .page-title,
    .page-head {
        h1,
        h3 {
            border: none;
        }
    }

    .std {

        img {
            max-width: 100%;
        }

        p {
            color: $c-text;
            font-family: $f-stack-serif;
            font-size: 14px;
            font-style: italic;
            line-height: 24px;
        }

        h1 {
            color: $c-text-primary;
        }

        h2 {
            color: $c-text-primary;
        }

        li {
            color: $c-text;
            font-family: $f-stack-serif;
            font-size: $f-size;
            font-style: italic;
            line-height: 24px;
        }
    }

    .col-left {
        &:after {
            @include clearfix;
        }
    }
}

.cms-no-route {
    .std {

        img {
            width: 100%;
        }
    }
}

.cms-page-view .std,
.cms-home .std,
.cms-no-route .std {
    .messages {
        margin-bottom: $box-spacing;

        ul {
            margin-bottom: 0;
        }

        li {
            list-style: none;
            margin-left: 0;
            font-style: normal;
            font-family: $f-stack-sans;
        }
    }
}

// ----------------------------------------------
// Accordition

#accordion {
    @include accordion;

    li {
        position: relative;
        overflow: hidden;

        .plusimage h2 {
            margin-right: $gap;
        }
    }
}

// ----------------------------------------------
// Sitemap

.page-sitemap {
    .links {
        display: block;
        text-align: right;
        margin: 0 20px 0 0;

        a { position: relative; }
    }

    .sitemap {
        padding: 0 20px;
        margin: 15px 0;
        font-size: $f-size-xs;

        li {
            margin: 3px 0;
        }
    }
}

// ----------------------------------------------
// Contact page

.contacts-index-index {
    h1 {
        border: none;
    }
}

#contactForm {
    padding-left: 40px;

    h2 {
        display: none;
    }

    &:after {
        @include clearfix;
    }

    .fieldset .legend + .required {
        margin: 0;
    }
}

@include bp(max-width, $bp-medium) {

    #contactForm {
        padding-left: 0;
    }
}

// ------------------------------
// Homepage
.cms-index-index.cms-home {
    .main-container {
        max-width: 100%;
        padding: 0;
    }
    .page-title { display: none; }

    #main-content {
        max-width: 1200px;
        margin: 0 auto;
        text-align: center;

        a { 
            display: inline-block; 

            img { width: 100%; }

            &:hover { opacity: 0.8; cursor: pointer; }

            &:nth-child(1) { width: 48.75%; margin-right: 0.6%; max-height: 350px; }
            &:nth-child(2) { width: 48.75%; max-height: 350px; }

            &:nth-child(1),
            &:nth-child(2) { 
                margin-bottom: 50px; 
                margin-top: 20px;
                max-height: 310px; 
                @include bp(max-width, $bp-large) { 
                    margin: 0;
                    width: 49.5%;
                    max-height: 220px; 
                } 
                @include bp(max-width, $bp-small) { 
                    width: 100%;
                    max-height: none;
                } 
            }

            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5),
            &:nth-child(6) { 
                width: 24%; margin-right: 0.5%; 
                @include bp(max-width, $bp-large) { 
                    margin: 0;
                    width: 24%;
                } 
                @include bp(max-width, $bp-small) { 
                    width: 49%;
                } 
                @include bp(max-width, $bp-xsmall) {  width: 48.5%; }
            }
            &:nth-child(3),
            &:nth-child(5) {
                margin-right: 1%;
            }
            &:nth-child(6) { margin-right: 0; }
        }
    }
} 
