/* ============================================ *
 * Footer
 * ============================================ */

/* -------------------------------------------- *
 * Footer
 */

.footer,
.footer button {
    font-family: $f-stack-special;
}

.footer {
    clear: both;
    width: 100%;
    padding-top: $trim;
    max-width: 1336px;
    margin: 0 auto;

    @include bp(max-width, $bp-xsmall) {
        padding-top: $trim-small;
    }

    .block-title {
        border: 0;
        padding: 3px 0;
    }

    .block-title strong {
        font-weight: normal;
    }

    .block-title,
    address {
        color: $c-text-primary;
    }

    .links {
        float: left;
        width: 17%;
        padding: 0 $trim 0 0;
        margin-bottom: $trim;

        li {
            word-break: break-all;
            line-height: 1.5;
            padding: 4px 0;
        }

        a, h3 {
            color: $c-footer-text;
            &:hover {
                color: $c-action;
                @include bp(max-width, $bp-large) { color: $c-white }
            }
        }
    }

    .block-subscribe {
        float: right;
        width: 320px;
        margin-bottom: $trim;
    }

    .links,
    .block-subscribe {
        font-size: $f-size;
    }

    .form-subscribe-header {
        display: none;
    }

    /* -------------------------------------------- *
     * Social icons
     */

    .links.social-media em {
        background-image: url(../images/social_icons.png);
        background-repeat: no-repeat;
        width: 13px;
        vertical-align: middle;
        display: inline-block;
        margin-right: 7px;
    }

    @include if-min-resolution(2) {
        .links.social-media em {
            background-image: url(../images/social_icons@2x.png);
            background-size: 50px 125px;
        }
    }

    @mixin footer-social-icon($class, $y, $height: 13) {
        .links.social-media em.#{$class} {
            background-position: 0 #{$y}px;
            height: #{$height}px;
        }

        .links.social-media a:hover em.#{$class} {
            background-position: -25px #{$y}px;
        }
    }

    @include footer-social-icon(facebook, 0);
    @include footer-social-icon(twitter, -24);
    @include footer-social-icon(youtube, -49);
    @include footer-social-icon(pinterest, -74, 15);
    @include footer-social-icon(rss, -99);

    // ---------------------------------------------

    .block-subscribe .input-box {
        float: left;
        width: percentage(210px/320px);
        padding-top: 0;
    }

    .block-subscribe .input-text {
        width: 100%;
        border-right: 0;
        border-radius: 0;
    }

    .block-subscribe .block-content {
        padding-top: 7px;

        &:after {
            @include clearfix;
        }
    }

    .block-subscribe .actions {
        float: left;
        width: percentage(110px/320px);
        margin: 0;
    }

    .block-subscribe .actions .button {
        height: 30px;
        line-height: 17px;
        float: left;
    }

    .bugs,
    address {
        clear: both;
    }

    .bugs {
        display: none;
    }

    .form-language,
    .currency-switcher,
    .store-switcher {
        clear: both;
        margin-bottom: $gap;
    }

    .form-language,
    .currency-switcher {
        display: none;
    }

    @include bp(max-width, $bp-xlarge) {
        $links-trim: percentage(30px / 960px);
        $links-width: (100 - ($links-trim * 3)) / 4;

        .links {
            width: $links-width;
            padding-right: 0;
            margin-right: $links-trim;
        }

        .links:nth-child(4) {
            margin-right: 0;
        }

        .block-subscribe {
            clear: both;
            float: none;
            margin: 0 auto $trim auto;
        }
    }


    @include bp(max-width, $bp-small) {
        $links-trim: percentage(15px / 480px);
        $links-width: (100 - $links-trim) / 2;

        .links {
            width: $links-width;
            margin-right: 0;
        }

        .links:nth-child(odd) {
            margin-right: $links-trim;
            clear: both;
        }

        .block-subscribe {
            clear: both;
            float: left;
            width: 100%;
        }
    }
}

address {
    background: $c-purple;
    color: $c-text-white;
    text-align: center;
    padding: 0 $trim;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    @include bp(max-width, $bp-large) { background: #a699c7; }
    @include bp(max-width, $bp-xxs) { font-size: $f-size-xs; } 
    .inner { 
        max-width: 1336px; 
        margin: 0 auto; 
        text-align: left;
        @include bp(max-width, $bp-large) { text-align: center; } 
    }
}
.social-icons {
    float: right;
    margin-top: 50px;
    @include bp(max-width, $bp-large) { display: none; } 
    img { display: inline-block; margin: 0 5px; }
}


.xxx{
	padding: 1999px;
	opacity: .15;
}

.karol{
	padding: 5px;
	opacity: .9;
	border: 1px solid;
}
.footer-container {
        background: #f7f6fc;
        max-width: none;
}
.footer-header {
    @include bp(min-width, $bp-large + 1) {
        display: none;
    }
}
